import BABYLON from 'experiment-babylon-js'
import {
  Array, // eslint-disable-line
  String, // eslint-disable-line
  hasConstructor,
  delay,
} from 'experiment-js'

import { setMargins } from '../functions/globalFunctions'


const awakenPause = function () {
  const stateManager = this.stateManager
  const levelObject = stateManager.get('levelObject')
  const GUI = stateManager.get('GUI')
  const R = this.R
  const state = this.state
  const pauseTime = stateManager.timeInMs
  const threshold = R.get.thresholdIdle

  stateManager.get('sounds').prediction.play()

  state.hitKeyTime = null
  state.initialGuiVisible = GUI.levelVisible

  GUI.backgroundFill = BABYLON.Canvas2D.GetSolidColorBrush(new BABYLON.Color4(0, 0, 0, 1))
  GUI.levelVisible = true

  const lastPauseInfo = stateManager.get('lastPauseInfo', { level: levelObject.level, time: pauseTime })
  if (lastPauseInfo.level !== levelObject.level) {
    stateManager.set('pauseTimeLeft', threshold)
  }

  const currentTimeLeft = stateManager.get('pauseTimeLeft', threshold)
  stateManager.set('lastPauseInfo', { level: levelObject.level, time: pauseTime })

  // datamanager get pause left
  const options = { id: 'pauseText',
    parent: GUI,
    marginAlignment: 'h: center, v: center',
    fontName: '30pt Arial',
    fontSignedDistanceField: true,
  }

  state.timerText = function () {
    const timeLeft = parseInt((currentTimeLeft - stateManager.timeInMs + pauseTime) / 1000, 10)
    if (timeLeft < 0) { return false }
    return R.get.timeLimitText.replace('{n}', String(timeLeft))
  }

  let t = 0
  const delayInMs = 200
  state.tic = function () {
    state.centerText.levelVisible = true
    const text = state.timerText()
    if (text === false) {
      stateManager.set('endReason', R.get.endReasonIdle.replace('{t}', (threshold / 1000)).replace('{l}', levelObject.level))
      stateManager.goToState(R.get.states_end)
    } else {
      state.centerText.text = text
      delay(delayInMs).then(() => {
        if (stateManager.currentStateKey === R.get.states_pause) { state.tic() }
      })
      t += delayInMs
      if (t >= 3000) {
        stateManager.get('sounds').prediction.play()
        t = 0
      }
    }
  }

  if (!hasConstructor(BABYLON.Text2D, state.centerText)) {
    state.topText = new BABYLON.Text2D(R.get.pauseText, options)
    setMargins(state.topText, { topPixels: 0, bottomPixels: 40, leftPixels: 0, rightPixels: 0 })
    state.centerText = new BABYLON.Text2D(state.timerText(), options)
  } else {
    state.topText.levelVisible = true
    state.centerText.levelVisible = true
    state.centerText.text = state.timerText
  }


  delay(200).then(() => {
    if (stateManager.currentStateKey === R.get.states_pause) { state.tic() }
  })
}

const pauseKeyDown = function () {
  const state = this.state
  const stateManager = this.stateManager
  const levelObject = stateManager.get('levelObject')
  const R = this.R
  const context = this
  const threshold = R.get.thresholdIdle

  if (state.hitKeyTime === null) { state.hitKeyTime = stateManager.timeInMs }

  state.timerText = function () {
    const timeLeft = Math.ceil((3000 - stateManager.timeInMs + state.hitKeyTime) / 1000, 10)
    if (timeLeft < 0) { return false }
    return R.get.pauseRestart.replace('{n}', String(timeLeft))
  }

  state.tic = function () {
    const text = state.timerText()
    if (text !== false) {
      state.centerText.text = text
      delay(200).then(() => {
        if (!document.hasFocus()) {
          const lastPauseInfo = stateManager.get('lastPauseInfo', { level: levelObject.level, time: stateManager.timeInMs })
          const previousTimeLeft = stateManager.get('pauseTimeLeft', threshold)
          const currentTimeLeft = previousTimeLeft - stateManager.timeInMs + lastPauseInfo.time

          stateManager.set('pauseTimeLeft', currentTimeLeft)
          awakenPause.bind(context)()
        } else if (stateManager.currentStateKey === R.get.states_pause) { state.tic() }
      })
    } else if (stateManager.frozenState) { stateManager.goToState(stateManager.frozenState) }
  }

  delay(200).then(() => {
    if (!document.hasFocus()) {
      awakenPause.bind(context)()
    } else if (stateManager.currentStateKey === R.get.states_pause) { state.tic() }
  })
}

const endPause = function () {
  const stateManager = this.stateManager
  const state = this.state
  const levelObject = stateManager.get('levelObject')
  const GUI = stateManager.get('GUI')
  const R = this.R
  const threshold = R.get.thresholdIdle

  GUI.backgroundFill = BABYLON.Canvas2D.GetSolidColorBrush(new BABYLON.Color4(1, 1, 1, 0))
  GUI.levelVisible = state.initialGuiVisible

  state.hitKeyTime = null
  state.topText.levelVisible = false
  state.centerText.levelVisible = false

  const lastPauseInfo = stateManager.get('lastPauseInfo', { level: levelObject.level, time: stateManager.timeInMs })
  const previousTimeLeft = stateManager.get('pauseTimeLeft', threshold)
  const currentTimeLeft = previousTimeLeft - stateManager.timeInMs + lastPauseInfo.time

  if (currentTimeLeft < -3) {
    stateManager.call('prematureEnd', { reason: R.get.prematureEndPauseAboveThreshold })
  } else {
    stateManager.set('pauseTimeLeft', currentTimeLeft)
  }
}


export { awakenPause, endPause, pauseKeyDown }



