const getTileMargins = function () {
  const stateManager = this.stateManager
  const R = this.R

  const canvas = (stateManager.get('elements2D')).canvas

  const tileSize = stateManager.call('getTileSize')

  const offset = R.get.tile_offset

  if (typeof canvas === 'undefined') {
    throw new Error('StateManager.getTilePositions: canvas is undefined.')
  }

  const margins = [
    { leftPixels: 0, topPixels: 0, rightPixels: ((tileSize.x / 2) + offset), bottomPixels: (tileSize.y / 2) + offset }, // top left 0
    { rightPixels: 0, topPixels: 0, leftPixels: ((tileSize.x / 2) + offset), bottomPixels: (tileSize.y / 2) + offset }, // top right 1
    { rightPixels: 0, bottomPixels: 0, leftPixels: ((tileSize.x / 2) + offset), topPixels: (tileSize.y / 2) + offset }, // bottom right 2
    { leftPixels: 0, bottomPixels: 0, rightPixels: ((tileSize.x / 2) + offset), topPixels: (tileSize.y / 2) + offset }, // bottom left 3
  ]

  return margins
}

const getTilePositions = function () {
  if (typeof this.taskObject === 'undefined') {
    throw new Error('stateManager.getTilePositions: this.taskObject is undefined')
  }
  const stateManager = this.stateManager
  const R = this.R

  const canvas = (stateManager.get('elements2D')).canvas

  const tileSize = stateManager.call('getTileSize')
  const center = {
    w: canvas.width / 2,
    h: canvas.height / 2,
  }
  const offset = R.get.tile_offset

  if (typeof canvas === 'undefined') {
    throw new Error('StateManager.getTilePositions: canvas is undefined.')
  }

  const positions = [
    new BABYLON.Vector2(center.w - ((tileSize.x / 2) + offset), center.h + ((tileSize.y / 2) + offset)), // one is top left
    new BABYLON.Vector2(center.w + ((tileSize.x / 2) + offset), center.h + ((tileSize.y / 2) + offset)),
    new BABYLON.Vector2(center.w + ((tileSize.x / 2) + offset), center.h - ((tileSize.y / 2) + offset)),
    new BABYLON.Vector2(center.w - ((tileSize.x / 2) + offset), center.h - ((tileSize.y / 2) + offset)), // 4 is bottom left

  ]

  return (positions)
}

const highlightPosition = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.highlightPosition: this.stateManager is undefined')
  }

  if (typeof options.position === 'undefined') {
    throw new Error('stateManager.highlightPosition: options.position is undefined')
  }

  return (this.stateManager.promise('drawTiles', {
    hightlight: options.position,
    type: (typeof options.type !== 'undefined') ?
      options.type : 'prediction',
  }))
}

const drawPredictionTiles = function ({ withKeys = false } = { withKeys: false }) {
  if (typeof this.taskObject === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: this.taskObject is undefined')
  } else {
    return (this.stateManager.promise('drawTiles', {
      type: 'prediction',
      withKeys,
    }))
  }
}

const setAgent = function (options = null) {
  if (typeof this.taskObject === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: this.taskObject is undefined')
  }

  if (typeof options.agent === 'undefined') {
    throw new Error('stateManager.setAgent: options.agent is undefined')
  }

  const stateManager = this.stateManager

  const elements2D = stateManager.get('elements2D')
  // const currentPosition = elements2D.agent.position
  const currentMargin = elements2D.agent.margin

  elements2D.agent.opacity = 0
  switch (options.agent) {
    case 'wolf':
      elements2D.agent = elements2D.wolfFaceSprite
      break
    case 'rabbit':
      elements2D.agent = elements2D.rabbitFaceSprite
      break
    default:
      throw new Error(`stateManager.setAgent: options.agent ${options.agent} is of invalid type.`)
  }

  elements2D.agentType = options.agent
  // elements2D.agent.position = currentPosition
  elements2D.agent.margin = currentMargin

  return (elements2D.tiles[options.positions])
}

const getAgentPositionOnTile = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: stateManager is undefined')
  }

  if (typeof options.tile === 'undefined') {
    throw new Error('stateManager.setAgent: tile is undefined')
  }

  const stateManager = this.stateManager

  const elements2D = stateManager.get('elements2D')
  const agent = elements2D.agent

  const scaledAgentSize = (new BABYLON.Vector2(agent.size.width, agent.size.height)).scaleInPlace(agent.scale)

  const tilePositions = stateManager.call('getTilePositions')

  const newPosition = tilePositions[options.tile].subtract(scaledAgentSize.scale(0.5))

  return newPosition
}

const getAgentMarginsOnTile = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: stateManager is undefined')
  }

  if (typeof options.tile === 'undefined') {
    throw new Error('stateManager.setAgent: tile is undefined')
  }

  const stateManager = this.stateManager

  // const elements2D = stateManager.get('elements2D')
  // const agent = elements2D.agent

  // const scaledAgentSize = (new BABYLON.Vector2(agent.size.width, agent.size.height)).scaleInPlace(agent.scale)

  const offsetH = 0// no need ... it is based on the center of the sprite... (stateManager.call('getTileSize').y - agent.size.height) / 4
  const offsetW = 0// (stateManager.call('getTileSize').x - agent.size.width) / 4

  const tileMargins = stateManager.call('getTileMargins')[options.tile]

  const newMargins = {
    leftPixels: (tileMargins.leftPixels === 0) ? 0 : tileMargins.leftPixels + offsetW,
    rightPixels: (tileMargins.rightPixels === 0) ? 0 : tileMargins.rightPixels + offsetW,
    topPixels: (tileMargins.topPixels === 0) ? 0 : tileMargins.topPixels + offsetH,
    bottomPixels: (tileMargins.bottomPixels === 0) ? 0 : tileMargins.bottomPixels + offsetH,
  }

  // const newPosition = tilePositions[options.tile].subtract(scaledAgentSize.scale(0.5))

  return newMargins
}

const moveAgentToTile = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: stateManager is undefined')
  }

  if (typeof options.tile === 'undefined') {
    throw new Error('stateManager.setAgent: tile is undefined')
  }
  const elements2D = this.stateManager.get('elements2D')
  const agent = elements2D.agent
  // agent.position = this.stateManager.call('getAgentPositionOnTile', options)
  agent.margin = _.extend(agent.margin, this.stateManager.call('getAgentMarginsOnTile', options))
  this.stateManager.set('lastMove', options.tile)

  if (typeof options.opacity !== 'undefined') {
    this.stateManager.call('setAgentOpacity', {
      opacity: options.opacity,
    })
  }

  return (agent)
}

const animateAgentToTile = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: stateManager is undefined')
  }

  if (typeof options.tile === 'undefined') {
    throw new Error('stateManager.setAgent: tile is undefined')
  }
  const elements2D = this.stateManager.get('elements2D')
  const agent = elements2D.agent
  const newPosition = this.stateManager.call('getAgentPositionOnTile', options)

  if (typeof options.opacity !== 'undefined') {
    this.stateManager.call('setAgentOpacity', {
      opacity: options.opacity,
    })
  }
  // TODO Do not dispose of the tiles, just play on opacity. TODO fix the animations

  const deferred = new Deferred()
  /* --- Create an animation that lasts 100ms of the target sliding to new position --- */
  const animation = new BABYLON.Animation('slideToPosition', 'position', 100, BABYLON.Animation.ANIMATIONTYPE_VECTOR2, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT)

  const keys = []
  keys.push({
    frame: 0,
    value: agent.position,
  })
  keys.push({
    frame: 10,
    value: newPosition,
  })

  animation.setKeys(keys)
  const end = new BABYLON.AnimationEvent(10, () => {
    deferred.resolve()
  }, true)

  animation.addEvent(end)
  agent.animations.push(animation)
  this.scene.beginAnimation(agent, 0, 11)
  return deferred.promise
  // TODO create a this.animate(object, property, keys, loop)
}

const setAgentOpacity = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.drawPredictionTiles: this.stateManager is undefined')
  }

  if (typeof options.opacity === 'undefined') {
    throw new Error('stateManager.setAgent: options.opacity is undefined')
  }

  const elements2D = this.stateManager.get('elements2D')
  const agent = elements2D.agent

  agent.opacity = options.opacity

  return (agent)
}

const setBlackScreen = function (options = null) {
  const baseOptions = { // TODO create a defaultOptions()
    on: true,
  }

  options = _.extend(baseOptions, options)
  const elements2D = this.stateManager.getGlobal('elements2D')
  const canvas = elements2D.canvas

  if (!_.has(elements2D, 'blackScreen')) {
    const blackScreen = new BABYLON.Rectangle2D({
      parent: canvas,
      id: 'blackScreen',
      position: new BABYLON.Vector2(0, 0),
      width: canvas.size.width,
      height: canvas.size.height,
      zOrder: 0,
    })

    elements2D.blackScreen = blackScreen
  }

  if (options.on) {
    elements2D.blackScreen.opacity = 1
  } else {
    elements2D.blackScreen.opacity = 0
  }
}

const hideAll = function () {
  const stateManager = this.stateManager

  const elements2D = stateManager.get('elements2D')

  elements2D.agent.levelVisible = false
  elements2D.cross.levelVisible = false
  elements2D.loader.levelVisible = false
  elements2D.tiles.forEach((t) => { t.levelVisible = false })
}

const showAll = function () {
  const stateManager = this.stateManager

  const elements2D = stateManager.get('elements2D')

  elements2D.agent.levelVisible = true
  elements2D.cross.levelVisible = true
  elements2D.loader.levelVisible = true
  elements2D.tiles.forEach((t) => { t.levelVisible = true })
}

const checkIdleAndConnection = function ({ deferred = new Deferred(), returnToState = null } = { deferred: new Deferred(), returnToState: null }) {
  const stateManager = this.stateManager
  const R = this.R
  const dataManager = this.dataManager
  const connection = this.connection
  returnToState = returnToState || stateManager.currentStateKey

  dataManager.query('getLastInteraction', null, connection, deferred)
  return deferred.promise.then((r) => {
    const delay = stateManager.timeInMs - r.lastInteraction
    const delayMin = delay / (60000)
    if (delay > 30 * 60 * 1000) {
      // TODO idle for too long
      debugWarn('running.goToNextPosition: idle for too long')
      stateManager.set('endReason', `You were idle for more than 30 min: ${delayMin} min`)
      stateManager.goToState(R.get.states_end)
    } else if (stateManager.currentStateKey !== returnToState) { stateManager.goToState(returnToState) }
  })
  .catch(() => {
    // TODO lost connection
    debugWarn('running.goToNextPosition: lost connection')

    if (stateManager.currentStateKey !== R.get.states_idle) {
      stateManager.call('hideAll')
      stateManager.onNext(this.R.get.events_unfrozen, () => { stateManager.call('showAll') })
      this.state.freeze()
      stateManager.goToState(R.get.states_idle)
    }

    this.taskObject.modal({ type: 'centralLarge', title: R.get.lostConnectionTitle, content: R.get.lostConnectionContent })
    .then(() => delay(50)).then(() => { stateManager.call('checkIdleAndConnection', { returnToState }) })
  })
}

export { redrawAssets,
  drawTiles,
  addButton,
  disposeOfButtons,
  clearTiles,
  getTilePositions,
  getTileMargins,
  getTileSize,
  highlightPosition,
  drawPredictionTiles,
  getAgentMarginsOnTile,
  setAgent,
  setMargins,
  moveAgentToTile,
  animateAgentToTile,
  getAgentPositionOnTile,
  setAgentOpacity,
  setBlackScreen,
  hideAll,
  showAll,
  checkIdleAndConnection }



