const promiseSegmentsAndProbesByBlock = function (options = null) {
  return new Promise((resolve) => {
    const baseOptions = {
      segmentSizes: [3, 4, 5],
      observationSegmentRepetitionPerBlock: 2,
      numberOfBlocks: 6,
      dominantProbability: 0.75,
      positions: [0, 1, 2, 3],
    }

    options = _.extend(baseOptions, options)

    const numberOfTimeAProbeIsRepeated = options.segmentSizes.length * options.observationSegmentRepetitionPerBlock
    const positions = options.positions

    // Number of dominant transition by probe position
    const numberOfDominant = math.floor(options.dominantProbability * numberOfTimeAProbeIsRepeated)

    // let segmentSizeSequences = [];
    let segmentSizeSequencesEvenlyDistributed = []
    let probeSequences = []
    let transitionTypes = []
    let blockArray = []
    for (let i = 0; i < options.numberOfBlocks; i++) {
      /* --- Generate the segment size sequence for the block --- */
      const segmentPermutations = samplePermutation(rep(options.segmentSizes, positions.length * options.observationSegmentRepetitionPerBlock))
      // segmentSizeSequences = segmentSizeSequences.concat(segmentPermutations);
      blockArray = blockArray.concat(rep(i, segmentPermutations.length))

      // Each position sees the same number of segment sizes
      let probeSequence = samplePermutation(rep(options.positions, numberOfTimeAProbeIsRepeated))

      // make sure there is no repeated probe just one after the other
      // const previousProbe = null
      let loop = 0
      while (loop < 100) {
        if (probeSequence.hasNeighbouringRepeat()) {
          probeSequence = samplePermutation(rep(options.positions, numberOfTimeAProbeIsRepeated))
        } else {
          break
        }
        loop += 1
      }

      // Distribute uniformely position with segment sizes.
      // You have the right number of probes but now but not association with segment length
      const segmentEvenlyPermutated = []
      const leftSegmentPermutations = positions.map(() => samplePermutation(rep(options.segmentSizes, options.observationSegmentRepetitionPerBlock)))

      for (let i = 0; i < probeSequence.length; i++) {
        segmentEvenlyPermutated[i] = leftSegmentPermutations[probeSequence[i]].pop()
        if (typeof segmentEvenlyPermutated[i] === 'undefined') {
          throw new Error('promiseSegmentsAndProbesByBlock: leftSegmentPermutations is empty cannot pop !')
        }
      }
      segmentSizeSequencesEvenlyDistributed = segmentSizeSequencesEvenlyDistributed.concat(segmentEvenlyPermutated)

      /* --- Concat the probe sequence to the array for all blocks --- */
      probeSequences = probeSequences.concat(probeSequence)

      /* --- Compute dominant transitions --- */
      const indices = _.range(probeSequence.length)
      const transitionType = rep(0, probeSequence.length)
      const dominantCount = [0, 0, 0, 0]
      while (indices.length) {
        const r = _.random(0, indices.length - 1)
        const index = indices.splice(r, 1)
        const position = probeSequences[index]
        if (dominantCount[position - 1] >= numberOfDominant) {
          transitionType[index] = 0
        } else {
          dominantCount[position - 1] += 1
          transitionType[index] = 1
        }
      }

      /* --- Concat dominant transitions --- */

      transitionTypes = transitionTypes.concat(transitionType)
    }

    resolve({
      segmentSizeSequences: segmentSizeSequencesEvenlyDistributed,
      block: blockArray,
      probeSequences,
      transitionTypes,
      numberOfProbePerBlock: options.segmentSizes.length * options.observationSegmentRepetitionPerBlock * options.positions.length,
      numberOfObservationPerBlock: _.sum(options.segmentSizes) * options.observationSegmentRepetitionPerBlock * options.positions.length,
    })
  })
}

const getFullSequenceObjectForRandomBlackoutBlock = function (sequence = mandatory(), options = null) {
  const baseOptions = {
    observePredictionPerBlock: 16,
    block: 0,
    blockType: 'random',
    segmentLength: {
      A: [5, 2, 3, 4, 2],
      B: [2, 3, 4, 5],
    },
    trialTypes: {
      o: 'observation',
      oP: 'observation_prediction',
      pBP: 'post_blackout_prediction',
      blackout: 'blackout',
    },
  }

  options = _.extend(baseOptions, options)

  const segAPermutations = samplePermutation(options.segmentLength.A)
  const segBPermutations = samplePermutation(options.segmentLength.B)
  const segmentSequence = []
  segAPermutations.map((v, i) => { //eslint-disable-line
    segmentSequence.push(segAPermutations[i])
    if (i < segBPermutations.length) {
      segmentSequence.push(segBPermutations[i])
    }
  })

  const blockSize = options.segmentLength.A.sum() + options.segmentLength.B.sum()
  const blackoutIndices = segmentSequence.integrate()
  const addPostBlackout = []
  let predictionIndices = _.range(0, blockSize).filter((v) => {
    if (blackoutIndices.indexOf(v) !== -1) {
      return false
    } else if (blackoutIndices.indexOf(v - 1) !== -1) {
      addPostBlackout.push(v)
      return false
    }
    return true
  })
  predictionIndices = samplePermutation(predictionIndices)
    .slice(0, options.observePredictionPerBlock)

  const fullSequence = sequence.slice()
  const fullBlockType = rep(options.blockType, blockSize)
  const fullBlock = rep(options.block, blockSize)
  const fullTrial = _.range(1, blockSize + 1)
  const fullTrialType = []
  for (let i = 0; i < sequence.length; i++) {
    if (predictionIndices.indexOf(i) !== -1) {
      fullTrialType.push(options.trialTypes.oP)
    } else if (addPostBlackout.indexOf(i) !== -1) {
      fullTrialType.push(options.trialTypes.pBP)
    } else if (blackoutIndices.indexOf(i) !== -1) {
      fullTrialType.push(options.trialTypes.blackout)
    } else {
      fullTrialType.push(options.trialTypes.o)
    }
  }

  return ({
    fullSequence,
    blockType: fullBlockType,
    block: fullBlock,
    trial: fullTrial,
    trialType: fullTrialType,
  })
}

const promiseFullSequenceForIntermitentPrediction = function (transitionMatrix = mandatory(), options = null) {
  transitionMatrix = getValidTransitionArray(transitionMatrix)
  return new Promise((resolve) => {
    const baseOptions = {
      sequenceLength: 180,
      sequenceALength: 96,
      sequenceBLength: 84,
      observePredictionPerBlock: 16,
      errorThreshold: 0.06,
      numberOfNewTransitions: 4,
      maxLoop: 100,
      segmentLength: {
        A: [5, 2, 3, 4, 2],
        B: [2, 3, 4, 5],
      },
      protectFirstBlock: true,
      trialTypes: {
        o: 'observation',
        oP: 'observation_prediction',
        pBP: 'post_blackout_prediction',
        blackout: 'blackout',
      },
    }

    options = _.extend(baseOptions, options)

    let sequenceA
    let sequenceB

    options.sequenceLength = options.sequenceALength
    return promiseSequenceFromTransitionMatrix(transitionMatrix, options)
      .then((result) => {
        const resultObject = getObservedAndDistanceMatrix(result, transitionMatrix)
        // const observedMatrix = resultObject.observedMatrix
        const distanceMatrix = resultObject.distanceMatrix

        debugWarn('promiseFullSequenceForIntermitentPrediction.sequenceA', math.sum(math.abs(distanceMatrix)))

        sequenceA = result
        options.sequenceLength = options.sequenceBLength

        return (promiseSequenceFromTransitionMatrix(transitionMatrix, options))
      })
      .then((result) => {
        const resultObject = getObservedAndDistanceMatrix(result, transitionMatrix)
        // const observedMatrix = resultObject.observedMatrix
        const distanceMatrix = resultObject.distanceMatrix

        debugWarn('promiseFullSequenceForIntermitentPrediction.sequenceB', math.sum(math.abs(distanceMatrix)))

        sequenceB = _.clone(result)

        // start loop for blackouts
        let currentBlock = -1
        let currentTrial = 0
        let segAIndex = 0
        let indicesBlackout = []

        let fullSequence = []
        let fullTrialType = [] // fill it in the end of each block
        let fullTrial = []
        let fullBlock = []

        let segmentsA = _.clone(options.segmentLength.A)
        let segmentsB = _.clone(options.segmentLength.B)
        const lenghtBlockA = segmentsA.sum()
        const blockLength = lenghtBlockA + segmentsB.sum()
        let lenghtSegmentB = samplePermutation(segmentsB)
        for (let i = 0; i < sequenceA.length; i++) {
          const tempBlock = Math.floor(i / lenghtBlockA)


          if (tempBlock !== currentBlock) {
            segmentsA = _.clone(options.segmentLength.A)
            segmentsB = _.clone(options.segmentLength.B)
            currentBlock = tempBlock
            currentTrial = 0
            segAIndex = 0
            if ((currentBlock === 0) && (options.protectFirstBlock)) {
              let first = segmentsA.shift()
              segmentsA[0] += first
              indicesBlackout = segmentsA.integrate()
              lenghtSegmentB = samplePermutation(segmentsB)
              first = lenghtSegmentB.shift()
              lenghtSegmentB[0] += first
            } else {
              indicesBlackout = samplePermutation(segmentsA).integrate()
              lenghtSegmentB = samplePermutation(segmentsB)
            }
          }

          fullTrial.push(currentTrial)
          fullSequence.push(sequenceA[i])
          fullBlock.push(currentBlock)
          segAIndex += 1
          currentTrial += 1

          if ((segAIndex !== lenghtBlockA) && (indicesBlackout.indexOf(segAIndex) !== -1)) {
            fullTrialType.push(options.trialTypes.blackout)
            const addedSegmentLenght = lenghtSegmentB.shift()
            const addedSegment = sequenceB.splice(0, addedSegmentLenght)
            const addTrials = _.range(currentTrial, currentTrial + addedSegmentLenght)
            const addedTrialType = rep(options.trialTypes.o, addedSegmentLenght)
            addedTrialType[0] = options.trialTypes.pBP
            const addedBlock = rep(currentBlock, addedSegmentLenght)

            fullSequence = fullSequence.concat(addedSegment)
            fullTrial = fullTrial.concat(addTrials)
            fullTrialType = fullTrialType.concat(addedTrialType)
            fullBlock = fullBlock.concat(addedBlock)

            currentTrial += addedSegment.length
          } else {
            fullTrialType.push(options.trialTypes.o)
          }
        }

        // Add observation_prediction to each block
        const nblocks = Math.floor(fullSequence.length / blockLength)
        for (let i = 0; i < nblocks; i++) {
          const blockTrialType = fullTrialType.slice(i * blockLength, (i + 1) * blockLength)
          const indicesPrediction = samplePermutation(blockTrialType.indicesOf(options.trialTypes.o)).slice(0, options.observePredictionPerBlock)
          for (let j = 0; j < indicesPrediction.length; j++) {
            const index = i * (blockLength + indicesPrediction[j])
            fullTrialType[index] = options.trialTypes.oP
          }
        }

        resolve({
          fullSequence,
          trial: fullTrial,
          trialType: fullTrialType,
          block: fullBlock,
          blockType: rep('normal', fullSequence.length),
        })
      })
  })
}

/**
 * From a transitionMatrix returns an object with two properties sequence and trialType. Each position is either learning, probe, or feedback.
 * @param  {array} transitionMatrix Array or matrix
 * @param  {[type]} options          [description]
 * @return {[type]}                  [description]
 */
const promiseFullSequenceForOneLevel = function (transitionMatrix = mandatory(), options = null) {
  return new Promise((resolve) => {
    /* --- Make sure the transitionMatrix is valid --- */
    transitionMatrix = getValidTransitionArray(transitionMatrix)
    const numberOfStates = math.size(transitionMatrix)[0]

    const baseOptions = {
      numberOfSequences: 8,
      insertRandomBlocksAt: [2, 6],
      randomBlocksDominantStates: [2, 2],
      randomBlocksDominantStateProbability: 0.4,
    }

    options = _.extend(baseOptions, options)

    /* --- Create random matrix --- */

    const randomBlocksNonDominantProbability = (1 - options.randomBlocksDominantStateProbability) / (numberOfStates - 1)
    const randomTransitionMatrix = getRandomTransitionMatrix(rep(randomBlocksNonDominantProbability, numberOfStates))
    randomTransitionMatrix[options.randomBlocksDominantStates] = options.randomBlocksDominantStateProbability

    /* ---
      Get the dominant transitions and non dominant transitions for each position
      This will help build the feedback sequence
     --- */

    // for random blocks
    const randomBlocksDominantState = matrix(options.randomBlocksDominantStates.length, numberOfStates, 0)
    const randomBlocksNonDominantStates = matrix(options.randomBlocksDominantStates.length, numberOfStates, rep(0, numberOfStates - 1))
    for (let i = 0; i < options.randomBlocksDominantStates.length; i++) {
      for (let j = 0; j < numberOfStates; j++) {
        randomBlocksDominantState[i][j] = options.randomBlocksDominantStates[i]
        randomBlocksNonDominantStates[i][j] = _.range(0, numberOfStates).filter(v => (v !== options.randomBlocksDominantStates[i]))
      }
    }

    // for non random blocks
    const dominantMatrix = getDominantCellByRow(transitionMatrix)
    const dominantTransition = []
    const nonDominantTransitions = []
    _.forEach(dominantMatrix, (value, index) => {
      dominantTransition[index] = vectorToPosition(value)
      nonDominantTransitions[index] = _.range(0, dominantMatrix.length).filter(v => ((v !== index) && (v !== dominantTransition[index])))
    })

    promiseSegmentsAndProbesByBlock(options)
      .then((segments) => {
        const numberOfRandomBlocks = options.insertRandomBlocksAt.constructor === Array ? options.insertRandomBlocksAt.length : 0

        promiseMultipleSequences(randomTransitionMatrix, { // TODO: replace by a specific randomsequencegenerator able to change the main state
          numberOfSequences: numberOfRandomBlocks,
          sequenceLength: segments.numberOfObservationPerBlock,
        })
          .then((randomSequences) => {
            promiseMultipleSequences(transitionMatrix, {
              numberOfSequences: options.numberOfSequences - numberOfRandomBlocks,
              sequenceLength: segments.numberOfObservationPerBlock,
            })
              .then((smoothedSequences) => {
                const numberOfBlocks = (segments.block.uniqueValues()).length
                if (smoothedSequences.length > numberOfBlocks) {
                  // More sequences than block, select the best sequences
                  const observedData = []
                  const errors = []
                  const kls = []
                  const sequenceObjects = []
                  let bestSequence = null // eslint-disable-line
                  let bestError = null
                  for (let i = 0; i < smoothedSequences.length; i++) {
                    observedData[i] = getObservedAndDistanceMatrix(smoothedSequences[i], transitionMatrix) // eslint-disable-line
                    errors[i] = observedData[i].totalError
                    kls[i] = matrixKlDivergence(observedData[i].observedMatrix, transitionMatrix)
                    sequenceObjects.push({
                      sequence: smoothedSequences[i], // eslint-disable-line
                      error: errors[i],
                    })
                    if ((bestError === null) || (errors[i] < bestError)) {
                      bestSequence = smoothedSequences[i] // eslint-disable-line
                      bestError = errors[i]
                    }
                  }

                  console.warn(_.sortBy(errors))
                  let smoothedSequences = _.sortBy(sequenceObjects, 'error').map(o => o.sequence)
                }

                debuglog(segments)

                // in case the array is not ordered properly do a by block iteration
                const blocksNumber = segments.block.uniqueValues()
                let fullSequenceBlock = []
                let fullSequenceBlockType = []
                let completeSequence = []
                let trialType = []
                let trials = []

                let smoothedSequencesIndex = 0
                let randomSequencesIndex = 0
                let sequence
                let block
                let blockIndices
                let probeTransitions
                let blockType
                let nonDominantIndices
                let currentDominantTransitions
                let currentNonDominantTransitions

                for (let i = 0; i < blocksNumber.length; i++) {
                  if (options.insertRandomBlocksAt.indexOf(i) !== -1) {
                    /* --- Random sequence processing --- */
                    sequence = randomSequences[randomSequencesIndex]

                    currentDominantTransitions = randomBlocksDominantState[randomSequencesIndex]
                    currentNonDominantTransitions = randomBlocksNonDominantStates[randomSequencesIndex]

                    blockType = 'random'

                    randomSequencesIndex += 1
                  } else {
                    /* --- Non random sequence processing --- */
                    sequence = smoothedSequences[smoothedSequencesIndex]

                    currentDominantTransitions = dominantTransition
                    currentNonDominantTransitions = nonDominantTransitions

                    blockType = 'normal'

                    smoothedSequencesIndex += 1
                  }

                  block = blocksNumber[i]
                  blockIndices = segments.block.indicesOf(block)
                  const segmentSizesOfBlock = _.clone(segments.segmentSizeSequences) // eslint-disable-line
                    .filter((value, index) => (blockIndices.indexOf(index) !== -1))

                  // Creates the probeTransitions feedback sequence
                  probeTransitions = []
                  nonDominantIndices = [_.random(0, 1), _.random(0, 1), _.random(0, 1), _.random(0, 1)]
                  const probeSequenceRemaining = _.clone(segments.probeSequences).filter((value, index) => {
                    // checks that the probe is part of the block
                    if (blockIndices.indexOf(index) !== -1) {
                      // checks wether the transition is dominant or non dominant
                      if (segments.transitionTypes[index] === 1) {
                        // transition is dominant, pushing a dominant feedback
                        probeTransitions.push(currentDominantTransitions[value])
                      } else {
                        // transition is non dominant, make sure to switch the non dominant transition so it is never twice the same consecutivelly
                        probeTransitions.push(currentNonDominantTransitions[value][nonDominantIndices[value]])

                        nonDominantIndices[value] += 1
                        if (nonDominantIndices[value] >= currentNonDominantTransitions[value].length) {
                          nonDominantIndices[value] = 0
                        }
                      }
                      return (true)
                    }
                    return (false)
                  })

                  let currentObservationIndex = 0
                  let lastTrial = 0
                  for (let j = 0; j < blockIndices.length; j++) {
                    const segmentIndex = blockIndices[j]
                    const endIndex = currentObservationIndex + segments.segmentSizeSequences[segmentIndex]
                    if (endIndex > sequence.length) {
                      throw new Error('StateManager.getFullSequenceForBlock: sequence length is not equal to the total number of observation generated by segment for block')
                    }

                    const segment = sequence.slice(currentObservationIndex, endIndex)
                    completeSequence = completeSequence.concat(segment)
                    trialType = trialType.concat(rep('learning', segment.length))
                    currentObservationIndex = endIndex

                    // let probeIndex = segmentIndex;
                    const probePosition = probeSequenceRemaining[j]
                    const feedback = probeTransitions[j]

                    // old way ... why ??
                    // TODO think of a way to separate probesequence indx and segment index?? why...
                    // let probeIndex = _.random(0, probeSequenceRemaining.length - 1);
                    // let probePosition = probeSequenceRemaining.splice(probeIndex, 1);
                    // let feedback = probeTransitions.splice(probeIndex, 1);

                    completeSequence = (completeSequence.concat(probePosition)).concat(feedback)
                    trialType = trialType.concat(['probe', 'feedback'])

                    fullSequenceBlock = fullSequenceBlock.concat(rep(block, segment.length + 2))
                    fullSequenceBlockType = fullSequenceBlockType.concat(rep(blockType, segment.length + 2))
                    // get trials
                    const N = segment.length + 2
                    const addTrials = Array(...new Array(N)).map((_, i) => i + lastTrial + 1)
                    lastTrial = addTrials[addTrials.length - 1]
                    trials = trials.concat(addTrials)

                    // trial = trial.concat([...Array(segment.length + 2).keys()].map(x => x+lastTrial))
                  }
                }

                resolve({
                  fullSequence: completeSequence,
                  trialType,
                  block: fullSequenceBlock,
                  blockType: fullSequenceBlockType,
                  trial: trials,
                })
              })
          })
      })
  })
}


const getFullSequenceObjectForClassicLevels = function (sequence, options = {}) {
  const baseOptions = {
    trialType: 'classic',
    blockType: 'normal',
    block: 0,
  }

  options = _.extend(baseOptions, options)

  let t = 0
  return ({
    fullSequence: sequence,
    block: rep([options.block], sequence.length),
    blockType: rep([options.blockType], sequence.length),
    trialType: rep([options.trialType], sequence.length),
    trial: rep([0], sequence.length).map(() => {
      t += 1
      return t
    }),
  })
}

const insertSequenceObjectAtBlock = function (baseObject = mandatory(), insertedObject = mandatory(), atBlocks = mandatory()) {
  const newObject = {
    fullSequence: [],
    block: [],
    trial: [],
    trialType: [],
    blockType: [],
  }

  if (atBlocks.constructor === Number) {
    atBlocks = [atBlocks]
  }

  let offset = 0
  let currentBlock = baseObject.block[0]
  let baseCurrentBlock = currentBlock
  for (let i = 0; i < baseObject.block.length; i++) {
    if (atBlocks.indexOf(baseObject.block[i]) !== -1) {
      // Get rid of this insert marker
      atBlocks = atBlocks.filter(v => (v !== baseObject.block[i]))
      const insertedBlock = insertedObject.block[offset]

      if (baseObject.block[i] !== 0) { currentBlock += 1 } // Allows to start at 0 if marker was at 0

      while (insertedObject.block[offset] === insertedBlock) {
        newObject.fullSequence.push(insertedObject.fullSequence[offset])
        newObject.block.push(currentBlock)
        newObject.trial.push(insertedObject.trial[offset])
        newObject.trialType.push(insertedObject.trialType[offset])
        newObject.blockType.push(insertedObject.blockType[offset])
        offset += 1
      }
    }

    if (baseObject.block[i] !== baseCurrentBlock) {
      currentBlock += 1
      baseCurrentBlock = baseObject.block[i]
    }

    newObject.fullSequence.push(baseObject.fullSequence[i])
    newObject.block.push(currentBlock)
    newObject.trial.push(baseObject.trial[i])
    newObject.trialType.push(baseObject.trialType[i])
    newObject.blockType.push(baseObject.blockType[i])
  }

  // Take care of an addition at the end - all remaining blocks of inserted object will be added
  if ((atBlocks.indexOf(baseObject.block.max() + 1) !== -1) && (offset < insertedObject.block.length)) {
    let insertedBlock = -1
    while (offset < insertedObject.block.length) {
      if (insertedBlock !== insertedObject.block[offset]) {
        currentBlock += 1
        insertedBlock = insertedObject.block[offset]
      }

      newObject.fullSequence.push(insertedObject.fullSequence[offset])
      newObject.block.push(currentBlock)
      newObject.trial.push(insertedObject.trial[offset])
      newObject.trialType.push(insertedObject.trialType[offset])
      newObject.blockType.push(insertedObject.blockType[offset])
      offset += 1
    }
  }

  return newObject
}

