const computeDurationEstimate = function (options = {}) {
  const parametersBase = {
    dominantProbability: 0.76,
    minNumberOfObservationsPerLearning: 3,
    maxNumberOfObservationsPerLearning: 7,
    observationDuration: 500,
    interObservationDuration: 50, // to add ?
    minTimeToPredict: 2000,
    maxTimeToPredict: 5000,
    feedbackDuration: 500,
    minISIAfterLearning: 1000,
    maxISIAfterLearning: 1500,
    minISIAfterFeedback: 3000,
    maxISIAfterFeedback: 5000,
    rabbitAutoTransition: false,
    rabbitDominantProbability: 0.6,
    observationSegmentSizes: [3, 4, 5],
    observationSegmentRepetitionPerBlock: 2,
    numberOfBlocks: 6,
    numberOfRandomBlocks: 2,
    feedbackType: 'transition',
  }

  options = _.extend(parametersBase, options)

  // number of observation = sum(segment sizes) * segment repetition per block
  const numberOfObservationPerLevel = math.sum(options.observationSegmentSizes) * options.observationSegmentRepetitionPerBlock

  // duration of one observation = observationDuration (add inter observation duration ?)
  const oneObservationDuration = options.observationDuration

  // observation duration for one block
  const observationsDurationPerBlock = numberOfObservationPerLevel * oneObservationDuration

  // number of prediction per block
  const numberOfPredictionEventsPerBlock = options.observationSegmentSizes.length * options.observationSegmentRepetitionPerBlock

  // min max and mean duration of one prediction event stored in a matrix
  // timeDurationMatrix 4*2 min, max
  const timeDurationMatrix = math.matrix([
    [options.minISIAfterLearning, options.maxISIAfterLearning],
    [options.minTimeToPredict, options.maxTimeToPredict],
    [options.feedbackDuration, options.feedbackDuration],
    [options.maxISIAfterFeedback, options.maxISIAfterFeedback],
  ])

  // factorMatrix 2*3 min max mean
  const factorMatrix = math.matrix([
    [1, 0, 1 / 2],
    [0, 1, 1 / 2],
  ])

  // predictionDurationMatrix 4*3
  const predictionDurationMatrix = math.multiply(timeDurationMatrix, factorMatrix)

  // sumPredictionDurationVector 1*3
  const sumPredictionDurationVector = math.multiply(math.matrix([1, 1, 1, 1]), predictionDurationMatrix)

  const predictionsDurationPerBlock = math.multiply(sumPredictionDurationVector, numberOfPredictionEventsPerBlock)

  // duration of one block
  const observationDurationMatrix = math.matrix(rep(observationsDurationPerBlock, 3))

  const durationPerBlock = math.add(observationDurationMatrix, predictionsDurationPerBlock)

  // let total duration for one level min max mean in miliseconds
  const totalDuration = (math.multiply(durationPerBlock, (options.numberOfBlocks + options.numberOfRandomBlocks))).toArray()

  return ({
    min: totalDuration[0],
    max: totalDuration[1],
    mean: totalDuration[2],
  })
}

