import { EventData } from 'experiment-js'

const awakenEnd = function () {
  const R = this.R
  const scene = this.scene
  this.stateManager.call('hideAll')
  scene.initialGui.levelVisible = false
  this.dataManager.authorize_manual_login = false
  this.dataManager.query('hasCheckpoint', { checkpoint: R.get.checkpoint_taskDone }, this.connection)
  .then((r) => {
    const event = new EventData(this.R.get.events_aknowledgedEnding)
    this.stateManager.onNext(this.R.get.events_aknowledgedEnding, () => {
      window.open('', '_self').close()
    })
    let reason = 'You completed the task !'
    let title = R.get.taskFinishedTitle
    let content = R.get.taskFinishedContent

    if (r.code !== R.get.checkpoint_taskDone) {
      reason = this.stateManager.get('endReason', '')
      title = R.get.taskStoppedTitle
      content = R.get.taskStoppedMessage
      if (reason !== '') {
        content += R.get.taskStoppedReason.replace('{t}', reason)
      }
      this.taskObject.setCheckpoint(R.get.checkpoint_taskEndNoComeback, reason)

      this.taskObject.modal({ type: 'centralLarge', title, content, event })
    } else {
      // this.dataManager.query('getFinalCode', null, this.connection)
      // .then((r) => {
      //   content = content.replace('{c}', r.code)
      this.taskObject.modal({ type: 'centralLarge', title, content, event })
      // })
    }
  })
}

export { awakenEnd } //eslint-disable-line



