import {
  EventData,
  Array, // eslint-disable-line
  String, // eslint-disable-line
  delay,
  debuglog,
  hasConstructor,
} from 'experiment-js'

import { SmartModal } from 'experiment-boxes'


const pageOrderTutorialOne = ['intro', 'disclaimer', 'trialInstruction', 'fourLevels', 'progressInstruction', 'keyboardInstruction', 'introToTryout']
const pageOrderTutorialFMRI = ['fmriIntro', 'fmriRabbit', 'fmriNoPrediction', 'lockedCross']

const awakeTutorial = function () {
  const currentTutorial = this.stateManager.get('currentTutorial', 1)

  const pageOrder = currentTutorial === 1 ? pageOrderTutorialOne : pageOrderTutorialFMRI

  this.stateManager.set('currentPage', pageOrder[0])

  const showIntro = new EventData(this.R.get.events_showModal, { page: pageOrder[0] })
  this.stateManager.addEvent(showIntro)


  this.stateManager.set('levelObject', { level: `tutorial-${currentTutorial}` })
  this.state.shouldPause = (currentTutorial !== 1)
}

const showModal = function ({ data: { page = 'intro' } }) {
  // if (this.taskObject.currentModal !== null) {
  //   try {
  //     this.taskObject.currentModal.modalBox.destroy() // TODO think about wether callback or not etc.
  //   } catch (e) {
  //     debugWarn(e)
  //   }
  // }

  const tutorials = this.R.get.tutorials
  delay(50).then(() => { // TODO Check why you have to do that ...
    if (tutorials.hasOwnProperty(page)) {
      const tutorial = tutorials[page]
      const event = new EventData(this.R.get.events_tutorialNext, { page })
      this.taskObject.modal({ type: 'centralLarge', title: tutorial.title.en, content: tutorial.content.en, event })
      this.taskObject.currentModal.modalBox.buttonText = 'Next'
    }
  })
}

const tutorialUnfrozen = function () {
  if (hasConstructor(Object, this.taskObject.currentModal) && hasConstructor(SmartModal, this.taskObject.currentModal.modalBox)) {
    this.taskObject.currentModal.modalBox.show()
  }
}

const showNextPage = function ({ data: { page: currentPage = this.stateManager.get('currentPage', 'intro') } }) {
  const currentTutorial = this.stateManager.get('currentTutorial', 1)
  const pageOrder = currentTutorial === 1 ? pageOrderTutorialOne : pageOrderTutorialFMRI
  const R = this.R

  const currentIndex = pageOrder.indexOf(currentPage)
  if (currentIndex !== -1) {
    if (currentIndex !== pageOrder.length - 1) {
      const showEvent = new EventData(R.get.events_showModal, { page: pageOrder[currentIndex + 1] })
      this.stateManager.addEvent(showEvent)
    } else {
      debuglog('state.showNextPage: end of tutorial.')
      this.stateManager.goToState(R.get.states_tryout, true) // TODO make it able to use the state object as well
    }
  } else {
    throw new Error('state.showNextPage: invalid currentPage.')
  }
}

export { showNextPage, showModal, awakeTutorial, tutorialUnfrozen }



